@import url(http://fonts.cdnfonts.com/css/product-sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu-wrapper {
  width: 100%;
  background-color: #121212;
  height: 75px;
  transition: all 0.5s ease-in-out;
}

.menu-wrapper ul {
  margin-top: 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.menu-wrapper ul li {
  list-style-type: none;
  color: #fff;
  margin: 10px;
  padding: 10px;
  cursor: pointer;
  border-radius: 10px;
  background-color: #121212;
}

.menu-wrapper ul li:hover {
  background-color: #1d1d1d;
}
.avatar-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.avatar {
  width: 325px;
  height: 325px;
  border-radius: 100%;
}
.project-wrapper {
  width: 98%;
  
}
.project-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 98%;
  background-color: #1d1d1d;
  margin: 10px;
  border-radius: 10px;
}
.projects-title {
  text-align: center;
}
.App {
  background-color: #121212;
  width: 100%;
  height: 100%;
}

.first-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.opacity-change {
  margin-top: 130px;
  height: 10px;
  -webkit-animation: opacityChange 5s infinite;
          animation: opacityChange 5s infinite;
  margin-bottom: 70px; 
}

@-webkit-keyframes opacityChange {
  0% {
    opacity: 0%;
  }
  50% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}

@keyframes opacityChange {
  0% {
    opacity: 0%;
  }
  50% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
h1, h2, h3, h4, h5, h6, p, li {
  color: white;
  font-family: 'Product Sans', sans-serif;
}
